<template>
    <div class="home">
        <Swiper></Swiper>
        <List :list="list" dataName="home"></List>
    </div>
</template>

<script scoped>
import Swiper from "../components/Swiper"
import List from "../components/List"

export default {
    components:{
        Swiper,List
    },
    data(){
        return{
            list:[]
        }
    },
    mounted(){
        axios({
            url:'/api/home',
             params:{_page:1,_limit:10}
        }).then(
            res=>this.list=res.data.data
            
        )
    }
}
</script>
